.main-content {
  padding-bottom: 50px;
}

.site-header {
  height: 100px;
}

.site-header__logo {
  margin-top: 27px;
}

.site-header__logo img {
  display: block;
  max-width: 100%;
  height: auto;
}

.site-header__nav {
  display: inline-block;
  vertical-align: top;
  margin: 42px 0 0;
  padding: 0;
  list-style-type: none;
}

.site-header__nav > li {
  display: inline-block;
  padding: 0 20px;
  position: relative;
  border-bottom: 10px solid transparent;
}

.site-header__nav > li a {
  font: 300 13px "Open Sans", sans-serif;
  color: #404040;
  text-transform: uppercase;
  text-decoration: none;
  display: block;
}

.site-header__nav > li a:hover {
  color: #328f40;
}

.site-header__nav .menu-item-has-children > a {
  padding-right: 10px;
  background: url(../img/menu-drop.png) right center no-repeat;
}

.site-header__nav .menu-item-has-children:hover .sub-menu {
  display: block;
}

.site-header__nav .menu-item-has-children:hover > a {
  color: #328f40;
}

.site-header__nav .sub-menu {
  position: absolute;
  list-style-type: none;
  margin: 0;
  padding: 0;
  top: 25px;
  background: #fff;
  width: 200px;
  padding: 20px 15px;
  left: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  display: none;
}

.site-header__nav .sub-menu li {
  margin-bottom: 5px;
}

.site-header__nav .sub-menu li:last-child {
  margin-bottom: 0;
}

.site-header__tel {
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  font: 26px "glober", sans-serif;
  color: #328f40;
  margin-top: 32px;
  margin-left: 10px;
}

.page__top {
  padding: 200px 0;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}

.page__top__inner {
  text-align: center;
  color: #fff;
  max-width: 680px;
  margin: auto;
}

.page__top-title {
  margin: 0;
  font: 400 48px "glober", sans-serif;
}

.page__top-subtitle {
  font: 400 16px/24px "Open Sans", sans-serif;
  margin: 20px 0;
}

.simple-section {
  padding: 40px 0;
}

.simple-title {
  font: 400 48px "glober", sans-serif;
  color: #43616f;
  text-align: center;
  margin: 0 0 50px;
}

.simple-title.simple-title--white {
  color: #fff;
}

.home-brands__list {
  display: flex;
  flex-wrap: wrap;
}

.home-brands__item {
  width: 20%;
  padding: 50px 10px;
  display: flex;
  align-items: center;
}

.home-brands__item img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}

.simple-subheading {
  font: 400 16px/24px "Open Sans", sans-serif;
  color: #fff;
  text-align: center;
  max-width: 520px;
  margin: 20px auto;
}

.home-complex {
  background: url(../img/complex-bg.jpg) top center no-repeat;
  background-size: cover;
}

.home-complex .simple-title {
  margin-bottom: 10px;
}

.home-complex__list {
  display: flex;
  flex-wrap: wrap;
  margin-top: 60px;
}

.home-complex__item {
  width: 20%;
  padding: 0 20px;
  color: #fff;
  position: relative;
}

.home-complex__item-ico {
  height: 90px;
}

.home-complex__item-ico img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}

.home-complex__item-text {
  font: 400 14px/21px "Open Sans", sans-serif;
  color: #fff;
  display: block;
  text-align: center;
  max-width: 175px;
  margin: auto;
  text-decoration: underline;
}

.home-blog .slick-list {
  margin: 0 -20px;
}

.home-blog__slider-col {
  margin: 0 20px;
}

.home-blog .slick-dots {
  margin: 50px 0 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}

.home-blog .slick-dots li {
  display: inline-block;
  margin: 0 5px;
}

.home-blog .slick-dots .slick-active button {
  background: #43616f;
}

.home-blog .slick-dots button {
  padding: 0;
  text-indent: -99999px;
  font-size: 0px;
  display: block;
  width: 5px;
  height: 5px;
  background: #d9d9d9;
  border: none;
  outline: 0;
  cursor: pointer;
}

.full-link {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
}

.blog__item {
  margin-bottom: 40px;
  display: flex;
  border: 1px solid #e6e6e6;
  position: relative;
}

.blog__item:last-child {
  margin-bottom: 00px;
}

.blog__item-img {
  background-size: cover;
  background-position: center center;
  width: 210px;
}

.blog__item-content {
  flex: 1;
  padding: 30px 35px;
  position: relative;
}

.blog__item-title {
  margin: 0;
  font: 600 16px/22px "Open Sans", sans-serif;
  color: #404040;
  min-height: 44px;
}

.blog__item-description {
  font: 400 14px/21px "Open Sans", sans-serif;
  color: #666666;
  min-height: 84px;
}

.blog__item-date {
  font: 400 12px "Open Sans", sans-serif;
  color: #999999;
  float: left;
}

.blog__item-more {
  float: right;
  font: 400 12px "Open Sans", sans-serif;
  text-transform: uppercase;
  color: #3a9360;
  text-decoration: none;
  display: block;
  padding-right: 20px;
  min-height: 14px;
  background: url(../img/more-arr.png) right center no-repeat;
}

.footer-top {
  background: #3c4252;
  padding: 20px 0;
  border-top: 1px solid #636875;
  border-bottom: 1px solid #2b2f3b;
}

.footer-top__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-top .footer-text {
  padding-left: 18px;
  font: 400 14px "Open Sans", sans-serif;
  color: #77838d;
  margin: 0;
  text-decoration: none;
}

.footer-top .footer-loc {
  background: url(../img/footer-loc.png) left center no-repeat;
}

.footer-top .footer-tel {
  background: url(../img/footer-tel.png) left center no-repeat;
}

.footer-top .footer-email {
  background: url(../img/footer-email.png) left center no-repeat;
}

.footer-top .footer-social {
  overflow: hidden;
}

.footer-top .footer-social a {
  display: block;
  float: left;
  margin-left: 10px;
  width: 17px;
  height: 17px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: .7;
}

.footer-top .footer-social a:hover {
  opacity: 1;
}

.footer-bottom {
  padding: 10px 0;
  background: #363d4a;
  border-top: 1px solid #4a515c;
}

.footer-bottom p {
  margin: 0;
  font: 400 12px "Open Sans", sans-serif;
  color: #556473;
}

.footer-bottom a {
  color: #379b61;
}

.blog__page {
  padding-top: 80px;
  display: flex;
}

.blog-sidebar {
  width: 300px;
  float: right;
}

.blog__page-content {
  flex: 1;
  padding-right: 70px;
}

.blog__page-content .blog__item:first-child {
  display: block;
}

.blog__page-content .blog__item:first-child .blog__item-img {
  height: 330px;
  width: 100%;
}

.sidebar__block {
  padding: 45px 0;
  border-top: 1px solid #e6e6e6;
}

.sidebar__block:first-child {
  padding-top: 0;
  border: none;
}

.sidebar__block-title {
  font: 700 14px "Open Sans", sans-serif;
  color: #43616f;
  margin: 0 0 25px;
  text-transform: uppercase;
}

.sidebar-menu {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.sidebar-menu li {
  margin-bottom: 15px;
}

.sidebar-menu li:last-child {
  margin-bottom: 0;
}

.sidebar-menu a {
  font: 400 14px "Open Sans", sans-serif;
  color: #666666;
  text-decoration: none;
}

.sidebar-menu a:hover {
  text-decoration: underline;
}

.sidebar-popular {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.sidebar-popular__img {
  display: block;
  float: left;
  max-width: 50px;
  height: auto;
}

.sidebar-popular li {
  margin-bottom: 25px;
  position: relative;
}

.sidebar-popular li:hover .sidebar-popular__text {
  text-decoration: underline;
}

.sidebar-popular li:last-child {
  margin-bottom: 0;
}

.sidebar-popular__text {
  margin: 0 0 0 75px;
  font: 300 14px "Open Sans", sans-serif;
  color: #404040;
}

.sidebar__form {
  background: #43616f;
  border: 1px solid #3c4252;
  padding: 30px;
}

.sidebar__form-title {
  font: 700 14px "Open Sans", sans-serif;
  color: #fff;
  text-align: center;
  margin: 0 0 30px;
  text-transform: uppercase;
}

.sidebar__form-input {
  display: block;
  width: 100%;
  height: 40px;
  background-color: #375360;
  border: 1px solid #3c4252;
  box-shadow: inset 0 2px 0 rgba(67, 97, 111, 0.75);
  outline: 0;
  margin-bottom: 20px;
  padding: 0 15px;
  color: #fff;
  font: 300 14px "Open Sans", sans-serif;
}

.sidebar__form .btn {
  padding: 0;
  margin-top: 30px;
}